import React from 'react'

export default function Todo({ item, setTodos,todos,filterTodo, setFilterTodo }) {
    const completeHandler = () => {
        setTodos(todos.map(todo => {
            if (todo.id === item.id) {
                return {
                    ...todo,
                    completed: !item.completed
                }
            }
            else {
                return {
                    ...todo
                }
            }
        }));
        setFilterTodo(filterTodo.map(todo => {
            if (todo.id === item.id) {
                return {
                    ...todo,
                    completed: !item.completed
                }
            }
            else {
                return {
                    ...todo
                }
            }
        }));
    }

    const deleteHandler = () => {
        setTodos(todos.filter(todo => todo.id !== item.id));
        setFilterTodo(filterTodo.filter(todo => todo.id !== item.id))
    }


    return (
        <div className="todo-item">
                    <li className={`${item.completed ? "completed" : ""}`}>{ item.text }</li>
                <button className="check" onClick={completeHandler}>
                  <i className="fas fa-check"></i>
                </button >
                <button className="trash"  onClick={deleteHandler}>
                  <i className="fas fa-trash "></i>
                </button>
              </div>
    )
}
